.app-page {
  overflow-x: hidden;
}
.navbar {
  display: flex;
  flex-direction: row;
  text-align: center;
  margin-top: 3%;
  margin-bottom: 3%;
  margin-left: 40%;
  background-color: none;
  position: absolute;
  color: white;
  font-family: Impact, fantasy;
  font-size: x-large;
  font-style: normal;
}
#resume,
#contact {
  margin-top: 5%;
}

li {
  margin-left: -70%;
  list-style: none;
}
li:hover {
  color: darkgoldenrod;
}
hr {
  height: 1px;
  background-color: black;
  border: none;
  width: 40%;
  color: black;
}
p {
  font-family: cursive;
  font-size: large;
}
h4 {
  font-family: sans-serif;
}
span {
  font-family: cursive;
}

@media only screen and (max-width: 600px) {
  .app-page {
    overflow-x: hidden;
  }
  span {
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
  }
  p {
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
    font-size: large;
  }
  #resume {
    margin-top: 60%;
  }
  #contact {
    margin-top: 105%;
  }
  .btns {
    margin-right: 20%;
    margin-left: 0%;
    justify-content: space-between;
  }
  .image {
    margin-top: 10%;
    margin-right: -10% !important;
    padding: 5% !important;
  }
  #btn1 {
    margin-right: 20%;
    width: 5%;
  }
  #btn2 {
    margin-left: -12%;
  }
  .navbar {
    text-align: center;
    font-size: small;
  }
  .myname {
    font-size: large !important;
  }
  .home-page {
    align-items: center !important;
    align-self: center;
    text-align: center;
  }
  .css-19kzrtu {
    padding: 0px;
  }
}
